.contactForm-ctn {
  width: 100%;
  height: calc(100vh - 64px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #0E101C;
  color: #fff;
}

.contactForm-ctn > h1 {
  font-size: 3rem;
  letter-spacing: 0.2rem;
  font-weight: 100;
  text-align: left;
}

.contactForm-ctn .contactForm {
  display: flex;
  flex-direction: column;
  width: 30vw;
}

.contactForm-ctn .contactForm .item {
  margin: .5rem 0;
  width: 100%;
}

.contactForm input[type="text"], 
.contactForm input[type="email"] {
  width: 100%;
  height: 2rem;
  padding: .4rem .7rem;
}

.contactForm textarea {
  width: 100%;
  height: 5rem;
  margin: 0;
  padding: .4rem .7rem;
}

.contactForm-ctn .contactForm .item input,
.contactForm-ctn .contactForm .item textarea {
  border: none;
  border-radius: 5px;
  outline: none;
  background-color: #D4D3DC;;
  color: black;
}

.contactForm .checkbox-ctn input {
  margin-right: 15px;
}

.contactForm .checkbox-ctn p {
  text-align: right;
}

.contactForm > #submit-btn {
  width: 10rem;
  height: 3rem;
  margin: 0 auto;
  background: var(--primary);
  color: white;
  border: none;
  border-radius: 10px;
}

.contactForm > #submit-btn:hover {
  border: 1px solid;
  background: white;
  color: var(--primary);
  cursor: pointer;
}

.checkbox-ctn div input[type="checkbox"],
.checkbox-ctn div input[type="checkbox"] label {
  cursor: pointer;
}